import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Mic, 
  MicOff, 
  Phone, 
  PhoneOff, 
  Video, 
  VideoOff, 
  Settings, 
  MessageSquare,
  User,
  Bot,
  Volume2,
  VolumeX,
  Camera,
  CameraOff,
  MoreVertical,
  Minimize2,
  Maximize2
} from 'lucide-react';
import { useAuth } from '../hooks/useAuth';
import { useChat } from '../contexts/ChatContext';
import { toast } from 'react-hot-toast';

interface Message {
  id: string;
  text: string;
  sender: 'user' | 'ai';
  timestamp: Date;
  metadata?: {
    language?: string;
    confidence?: number;
    voice_ready?: string[];
    intent?: string;
    suggestions?: string[];
  };
}

const Chat: React.FC = () => {
  const { user } = useAuth();
  const { messages, sendMessage, isConnected } = useChat();
  const [inputText, setInputText] = useState('');
  const [selectedLanguage, setSelectedLanguage] = useState('hi');
  const [isRecording, setIsRecording] = useState(false);
  const [mediaRecorder, setMediaRecorder] = useState<MediaRecorder | null>(null);
  const [audioChunks, setAudioChunks] = useState<Blob[]>([]);
  const [isListening, setIsListening] = useState(false);
  const [voicePrompt, setVoicePrompt] = useState('    ');
  
  // Video call states
  const [isVideoOn, setIsVideoOn] = useState(true);
  const [isAudioOn, setIsAudioOn] = useState(true);
  const [isMuted, setIsMuted] = useState(false);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [showControls, setShowControls] = useState(true);
  const [isKrishiMitraSpeaking, setIsKrishiMitraSpeaking] = useState(false);
  const [currentSpeaker, setCurrentSpeaker] = useState<'farmer' | 'krishimitra' | null>(null);
  const [realtimeSession, setRealtimeSession] = useState<any>(null);
  const [isRealtimeActive, setIsRealtimeActive] = useState(false);
  
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const videoRef = useRef<HTMLVideoElement>(null);
  const krishiMitraVideoRef = useRef<HTMLVideoElement>(null);

  const languages = [
    { code: 'hi', name: '', flag: '' },
    { code: 'en', name: 'English', flag: '' },
    { code: 'bn', name: '', flag: '' },
    { code: 'ta', name: '', flag: '' },
    { code: 'te', name: '', flag: '' },
    { code: 'mr', name: '', flag: '' },
    { code: 'gu', name: '', flag: '' },
    { code: 'pa', name: '', flag: '' },
    { code: 'or', name: '', flag: '' },
    { code: 'ml', name: '', flag: '' },
    { code: 'kn', name: '', flag: '' }
  ];

  useEffect(() => {
    // Add welcome message
    if (messages.length === 0) {
      addMessage({
        id: 'welcome',
        text: ` ${user?.name || ''}!  KrishiMitra      ?     - , ,  ,       `,
        sender: 'ai',
        timestamp: new Date(),
        metadata: {
          voice_ready: [` ${user?.name || ''}!  KrishiMitra      ?`, "    - , ,  ,       "],
          intent: 'welcome',
          suggestions: ['   ', '   ', '  ', '   ']
        }
      });
    }
  }, [messages.length, user?.name]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const addMessage = (message: Message) => {
    // This would be handled by the chat context
    console.log('Adding message:', message);
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;
    
    setCurrentSpeaker('farmer');
    setIsKrishiMitraSpeaking(false);
    
    await sendMessage(inputText, selectedLanguage);
    setInputText('');
    
    // Simulate KrishiMitra response
    setTimeout(() => {
      setCurrentSpeaker('krishimitra');
      setIsKrishiMitraSpeaking(true);
      
      // Stop speaking after 3 seconds
      setTimeout(() => {
        setIsKrishiMitraSpeaking(false);
        setCurrentSpeaker(null);
      }, 3000);
    }, 1000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const startVoiceRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(stream);
      const chunks: Blob[] = [];
      
      recorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunks.push(event.data);
        }
      };
      
      recorder.onstop = async () => {
        const audioBlob = new Blob(chunks, { type: 'audio/wav' });
        await processVoiceInput(audioBlob);
        stream.getTracks().forEach(track => track.stop());
      };
      
      setMediaRecorder(recorder);
      setAudioChunks(chunks);
      recorder.start();
      setIsRecording(true);
      setIsListening(true);
      setVoicePrompt('  ...');
      setCurrentSpeaker('farmer');
      toast.success(' KrishiMitra    ...');
      
      // Auto-stop after 15 seconds
      setTimeout(() => {
        if (recorder.state === 'recording') {
          stopVoiceRecording();
        }
      }, 15000);
      
    } catch (error) {
      console.error('Error starting voice recording:', error);
      toast.error('   ');
    }
  };

  const stopVoiceRecording = () => {
    if (mediaRecorder && mediaRecorder.state === 'recording') {
      mediaRecorder.stop();
      setIsRecording(false);
      setIsListening(false);
      setVoicePrompt('    ');
      setCurrentSpeaker(null);
      toast.success('   ');
    }
  };

  const processVoiceInput = async (audioBlob: Blob) => {
    try {
      toast.success(' OpenAI Voice API      ...');
      
      // Create FormData for file upload
      const formData = new FormData();
      formData.append('audio_file', audioBlob, 'voice_input.wav');
      formData.append('language', selectedLanguage);
      
      // Send to OpenAI Voice API endpoint
      const response = await fetch('/api/v1/voice/speech-to-text', {
        method: 'POST',
        body: formData,
      });
      
      if (!response.ok) {
        throw new Error('Voice processing failed');
      }
      
      const result = await response.json();
      
      if (result.text) {
        // Set the transcribed text and send the message
        setInputText(result.text);
        await sendMessage(result.text, result.language || selectedLanguage);
        
        toast.success(' OpenAI Whisper     !');
      } else {
        throw new Error('No text detected');
      }
      
    } catch (error) {
      console.error('Error processing voice input:', error);
      
      // Fallback to mock processing for demo
      toast.success(' OpenAI API   ,      ...');
      
      const mockVoiceTexts = {
        'hi': '         ?',
        'bn': '       ?',
        'ta': '    ?',
        'te': '    ?',
        'mr': '     ?',
        'gu': '      ?',
        'pa': '        ?',
        'or': '     ?',
        'ml': '    ?',
        'kn': '    ?'
      };
      
      const mockText = mockVoiceTexts[selectedLanguage as keyof typeof mockVoiceTexts] || mockVoiceTexts['hi'];
      setInputText(mockText);
      await sendMessage(mockText, selectedLanguage);
      
      toast.success('      !');
    }
  };

  const handleVoiceToggle = () => {
    if (isRecording) {
      stopVoiceRecording();
    } else {
      startVoiceRecording();
    }
  };

  const handleQuickAction = async (action: string) => {
    const message = `Tell me about ${action.toLowerCase()}`;
    setInputText(message);
    await sendMessage(message, selectedLanguage);
  };

  const toggleVideo = () => {
    setIsVideoOn(!isVideoOn);
    toast.success(isVideoOn ? '  ' : '  ');
  };

  const toggleAudio = () => {
    setIsAudioOn(!isAudioOn);
    setIsMuted(!isMuted);
    toast.success(isMuted ? '  ' : '  ');
  };

  const toggleFullscreen = () => {
    setIsFullscreen(!isFullscreen);
    toast.success(isFullscreen ? '   ' : '   ');
  };

  const startRealtimeSession = async () => {
    try {
      toast.success(' KrishiMitra Realtime session    ...');
      
      const response = await fetch('/api/v1/voice/realtime/session', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          voice: 'alloy',
          language: selectedLanguage,
          user_id: user?.id,
          farmer_context: {
            location: 'Punjab, India',
            crops: ['wheat', 'rice', 'cotton'],
            experience: '15 years',
            land_size: '10 acres',
            current_concerns: ['loan repayment', 'crop selection', 'market prices']
          }
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to create realtime session');
      }

      const sessionData = await response.json();
      setRealtimeSession(sessionData);
      setIsRealtimeActive(true);
      
      toast.success(' KrishiMitra Realtime session  !     ');
      
    } catch (error) {
      console.error('Error starting realtime session:', error);
      toast.error(' Realtime session         ');
    }
  };

  const stopRealtimeSession = () => {
    setRealtimeSession(null);
    setIsRealtimeActive(false);
    toast.success(' KrishiMitra Realtime session    ');
  };

  const quickActions = [
    { icon: <User className="w-5 h-5" />, text: ' ', color: 'bg-green-500 hover:bg-green-600' },
    { icon: <Bot className="w-5 h-5" />, text: ' ', color: 'bg-blue-500 hover:bg-blue-600' },
    { icon: <Volume2 className="w-5 h-5" />, text: ' ', color: 'bg-yellow-500 hover:bg-yellow-600' },
    { icon: <Camera className="w-5 h-5" />, text: ' ', color: 'bg-purple-500 hover:bg-purple-600' }
  ];

  return (
    <div className={`min-h-screen bg-gradient-to-br from-green-50 to-blue-50 ${isFullscreen ? 'fixed inset-0 z-50' : ''}`}>
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-3">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-gradient-to-r from-green-500 to-blue-500 rounded-full flex items-center justify-center">
                <Bot className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-lg font-semibold text-gray-900">KrishiMitra Video Call</h1>
                <p className="text-sm text-gray-500">
                  {isConnected ? ' Connected' : ' Disconnected'}
                </p>
              </div>
            </div>
            
            <div className="flex items-center space-x-2">
              <select
                value={selectedLanguage}
                onChange={(e) => setSelectedLanguage(e.target.value)}
                className="px-3 py-1 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-green-500"
              >
                {languages.map((lang) => (
                  <option key={lang.code} value={lang.code}>
                    {lang.flag} {lang.name}
                  </option>
                ))}
              </select>
              
              <button
                onClick={toggleFullscreen}
                className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors"
              >
                {isFullscreen ? <Minimize2 className="w-5 h-5" /> : <Maximize2 className="w-5 h-5" />}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Video Call Interface */}
      <div className="max-w-7xl mx-auto px-4 py-6">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 h-[calc(100vh-200px)]">
          
          {/* Farmer's Video Panel */}
          <div className="relative bg-gray-900 rounded-xl overflow-hidden shadow-2xl">
            <div className="absolute inset-0 bg-gradient-to-br from-green-600/20 to-blue-600/20 z-10" />
            
            {/* Video Placeholder */}
            <div className="absolute inset-0 flex items-center justify-center">
              {isVideoOn ? (
                <div className="text-center">
                  <div className="w-32 h-32 bg-gradient-to-r from-green-500 to-blue-500 rounded-full flex items-center justify-center mx-auto mb-4">
                    <User className="w-16 h-16 text-white" />
                  </div>
                  <p className="text-white text-lg font-medium">{user?.name || ''}</p>
                  <p className="text-gray-300 text-sm"> </p>
                </div>
              ) : (
                <div className="text-center">
                  <div className="w-32 h-32 bg-gray-700 rounded-full flex items-center justify-center mx-auto mb-4">
                    <VideoOff className="w-16 h-16 text-gray-400" />
                  </div>
                  <p className="text-gray-400 text-lg">  </p>
                </div>
              )}
            </div>
            
            {/* Speaking Indicator */}
            {currentSpeaker === 'farmer' && (
              <div className="absolute top-4 left-4 bg-green-500 text-white px-3 py-1 rounded-full text-sm font-medium animate-pulse">
                   
              </div>
            )}
            
            {/* Status Indicators */}
            <div className="absolute top-4 right-4 flex space-x-2">
              {!isAudioOn && (
                <div className="bg-red-500 text-white p-2 rounded-full">
                  <MicOff className="w-4 h-4" />
                </div>
              )}
              {!isVideoOn && (
                <div className="bg-red-500 text-white p-2 rounded-full">
                  <VideoOff className="w-4 h-4" />
                </div>
              )}
            </div>
            
            {/* Name Tag */}
            <div className="absolute bottom-4 left-4 bg-black/50 text-white px-3 py-1 rounded-lg">
              <span className="text-sm font-medium">{user?.name || ''}</span>
            </div>
          </div>

          {/* KrishiMitra's Video Panel */}
          <div className="relative bg-gray-900 rounded-xl overflow-hidden shadow-2xl">
            <div className="absolute inset-0 bg-gradient-to-br from-blue-600/20 to-purple-600/20 z-10" />
            
            {/* Video Placeholder */}
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="text-center">
                <div className={`w-32 h-32 rounded-full flex items-center justify-center mx-auto mb-4 transition-all duration-300 ${
                  isKrishiMitraSpeaking 
                    ? 'bg-gradient-to-r from-blue-500 to-purple-500 animate-pulse' 
                    : 'bg-gradient-to-r from-blue-600 to-purple-600'
                }`}>
                  <Bot className="w-16 h-16 text-white" />
                </div>
                <p className="text-white text-lg font-medium">KrishiMitra AI</p>
                <p className="text-gray-300 text-sm"> AI </p>
              </div>
            </div>
            
            {/* Speaking Indicator */}
            {currentSpeaker === 'krishimitra' && (
              <div className="absolute top-4 left-4 bg-blue-500 text-white px-3 py-1 rounded-full text-sm font-medium animate-pulse">
                   
              </div>
            )}
            
            {/* AI Status */}
            <div className="absolute top-4 right-4 flex space-x-2">
              <div className={`px-3 py-1 rounded-full text-sm ${
                isRealtimeActive 
                  ? 'bg-blue-500 text-white animate-pulse' 
                  : 'bg-green-500 text-white'
              }`}>
                {isRealtimeActive ? ' Realtime' : 'AI '}
              </div>
            </div>
            
            {/* Name Tag */}
            <div className="absolute bottom-4 left-4 bg-black/50 text-white px-3 py-1 rounded-lg">
              <span className="text-sm font-medium">KrishiMitra AI</span>
            </div>
          </div>
        </div>

        {/* Control Panel */}
        <div className="mt-6 bg-white rounded-xl shadow-lg p-6">
          <div className="flex items-center justify-between">
            
            {/* Left Controls */}
            <div className="flex items-center space-x-4">
              <button
                onClick={toggleAudio}
                className={`p-4 rounded-full transition-all duration-200 ${
                  isMuted 
                    ? 'bg-red-500 text-white hover:bg-red-600' 
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {isMuted ? <MicOff className="w-6 h-6" /> : <Mic className="w-6 h-6" />}
              </button>
              
              <button
                onClick={toggleVideo}
                className={`p-4 rounded-full transition-all duration-200 ${
                  !isVideoOn 
                    ? 'bg-red-500 text-white hover:bg-red-600' 
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {isVideoOn ? <Video className="w-6 h-6" /> : <VideoOff className="w-6 h-6" />}
              </button>
              
              <button
                onClick={handleVoiceToggle}
                className={`p-4 rounded-full transition-all duration-200 ${
                  isRecording 
                    ? 'bg-red-500 text-white hover:bg-red-600 animate-pulse' 
                    : 'bg-green-500 text-white hover:bg-green-600'
                }`}
              >
                {isRecording ? <MicOff className="w-6 h-6" /> : <Mic className="w-6 h-6" />}
              </button>
            </div>

            {/* Center - Call Controls */}
            <div className="flex items-center space-x-4">
              <button 
                onClick={stopRealtimeSession}
                className="p-4 bg-red-500 text-white rounded-full hover:bg-red-600 transition-colors"
              >
                <PhoneOff className="w-6 h-6" />
              </button>
              
              <button 
                onClick={startRealtimeSession}
                disabled={isRealtimeActive}
                className={`p-4 rounded-full transition-colors ${
                  isRealtimeActive 
                    ? 'bg-green-600 text-white cursor-not-allowed' 
                    : 'bg-green-500 text-white hover:bg-green-600'
                }`}
              >
                <Phone className="w-6 h-6" />
              </button>
            </div>

            {/* Right Controls */}
            <div className="flex items-center space-x-4">
              <button className="p-4 bg-gray-100 text-gray-700 rounded-full hover:bg-gray-200 transition-colors">
                <Settings className="w-6 h-6" />
              </button>
              
              <button className="p-4 bg-gray-100 text-gray-700 rounded-full hover:bg-gray-200 transition-colors">
                <MoreVertical className="w-6 h-6" />
              </button>
            </div>
          </div>

          {/* Quick Actions */}
          <div className="mt-6 pt-6 border-t border-gray-200">
            <h3 className="text-sm font-medium text-gray-700 mb-3"> </h3>
            <div className="flex flex-wrap gap-3">
              {quickActions.map((action, index) => (
                <button
                  key={index}
                  onClick={() => handleQuickAction(action.text)}
                  className={`${action.color} text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors flex items-center space-x-2`}
                >
                  {action.icon}
                  <span>{action.text}</span>
                </button>
              ))}
            </div>
          </div>

          {/* Text Input (Hidden by default, can be toggled) */}
          <div className="mt-4 pt-4 border-t border-gray-200">
            <div className="flex space-x-3">
              <input
                type="text"
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="     - , ,  ,   ..."
                className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent"
              />
              <button
                onClick={handleSendMessage}
                disabled={!inputText.trim()}
                className="px-6 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Footer */}
      <div className="bg-white border-t mt-6">
        <div className="max-w-7xl mx-auto px-4 py-3">
          <div className="flex items-center justify-between text-sm text-gray-500">
            <span>Powered by KrishiMitra AI</span>
            <span>OpenAI Voice API  Real-time AI Assistant</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Chat;
