import React, { createContext, useContext, useState, useEffect, useRef, ReactNode } from 'react';
import axios from 'axios';
import { useAuth } from './AuthContext';

interface Message {
  id: string;
  text: string;
  sender: 'user' | 'ai';
  timestamp: Date;
  type: 'text' | 'voice' | 'system';
  metadata?: {
    language?: string;
    confidence?: number;
    audioUrl?: string;
    voice_ready?: string[];
    intent?: string;
    suggestions?: string[];
  };
}

interface ChatContextType {
  messages: Message[];
  isLoading: boolean;
  sendMessage: (text: string, language: string) => Promise<void>;
  clearMessages: () => void;
  isConnected: boolean;
}

const ChatContext = createContext<ChatContextType | undefined>(undefined);

export const useChat = () => {
  const context = useContext(ChatContext);
  if (context === undefined) {
    throw new Error('useChat must be used within a ChatProvider');
  }
  return context;
};

interface ChatProviderProps {
  children: ReactNode;
}

export const ChatProvider: React.FC<ChatProviderProps> = ({ children }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const wsRef = useRef<WebSocket | null>(null);
  const { user } = useAuth();

  // Mock AI responses for demonstration
  const mockAIResponses = {
    'crop advice': '     ,    ,              ',
    'loan help': '     PM-Kisan , KCC                    ?',
    'market prices': '   :  2,100-2,300/quintal,  1,800-2,000/quintal,  1,500-1,700/quintal          ?',
    'risk alert': '    3             ',
    'farming calendar': '   :       ,    ,      ',
    'local schemes': '       :   ,  ,             ?'
  };

  useEffect(() => {
    // Add welcome message when component mounts
    addMessage({
      id: Date.now().toString(),
      text: ` ${user?.name || ''}!  KrishiMitra      ?     - , ,  ,       `,
      sender: 'ai',
      timestamp: new Date(),
      type: 'system',
      metadata: {
        voice_ready: [` ${user?.name || ''}!  KrishiMitra      ?`, "    - , ,  ,       "],
        intent: 'welcome',
        suggestions: ['   ', '   ', '  ', '   ']
      }
    });

    // Try to connect to WebSocket (optional)
    try {
      connectWebSocket();
    } catch (error) {
      console.log('WebSocket not available, using mock responses');
      setIsConnected(false);
    }

    return () => {
      if (wsRef.current) {
        wsRef.current.close();
      }
    };
  }, [user]);

  const connectWebSocket = () => {
    try {
      const wsUrl = process.env.REACT_APP_WS_URL || 'ws://localhost:8000/ws';
      const clientId = user?.id || 'anonymous';
      
      wsRef.current = new WebSocket(`${wsUrl}/${clientId}`);
      
      wsRef.current.onopen = () => {
        console.log('WebSocket connected');
        setIsConnected(true);
      };
      
      wsRef.current.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          handleWebSocketMessage(data);
        } catch (error) {
          console.error('Error parsing WebSocket message:', error);
        }
      };
      
      wsRef.current.onclose = () => {
        console.log('WebSocket disconnected');
        setIsConnected(false);
      };
      
      wsRef.current.onerror = (error) => {
        console.error('WebSocket error:', error);
        setIsConnected(false);
      };
    } catch (error) {
      console.error('Error connecting to WebSocket:', error);
      setIsConnected(false);
    }
  };

  const handleWebSocketMessage = (data: any) => {
    // Handle KrishiMitra response format
    if (data.text) {
      addMessage({
        id: Date.now().toString(),
        text: data.text,
        sender: 'ai',
        timestamp: new Date(),
        type: 'text',
        metadata: {
          language: data.language,
          confidence: data.confidence,
          voice_ready: data.voice_ready,
          intent: data.intent,
          suggestions: data.suggestions
        }
      });
    } else if (data.type === 'error') {
      addMessage({
        id: Date.now().toString(),
        text: ' ,         ',
        sender: 'ai',
        timestamp: new Date(),
        type: 'system'
      });
    }
  };

  const addMessage = (message: Message) => {
    setMessages(prev => [...prev, message]);
  };

  const sendMessage = async (text: string, language: string) => {
    if (!text.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text,
      sender: 'user',
      timestamp: new Date(),
      type: 'text',
      metadata: { language }
    };

    addMessage(userMessage);
    setIsLoading(true);

    try {
      // Try WebSocket first if available
      if (wsRef.current && wsRef.current.readyState === WebSocket.OPEN) {
        wsRef.current.send(JSON.stringify({
          message: text,
          language,
          user_id: user?.id
        }));
      } else {
        // Fallback to mock responses
        await sendMockResponse(text, language);
      }
    } catch (error) {
      console.error('Error sending message:', error);
      
      // Add error message
      addMessage({
        id: (Date.now() + 1).toString(),
        text: ' ,         ',
        sender: 'ai',
        timestamp: new Date(),
        type: 'system'
      });
    } finally {
      setIsLoading(false);
    }
  };

  const sendMockResponse = async (text: string, language: string) => {
    // Simulate KrishiMitra processing delay
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000));
    
    // Generate contextual response
    let response = '';
    const lowerText = text.toLowerCase();
    
    if (lowerText.includes('crop') || lowerText.includes('')) {
      response = mockAIResponses['crop advice'];
    } else if (lowerText.includes('loan') || lowerText.includes('') || lowerText.includes('')) {
      response = mockAIResponses['loan help'];
    } else if (lowerText.includes('market') || lowerText.includes('') || lowerText.includes('')) {
      response = mockAIResponses['market prices'];
    } else if (lowerText.includes('risk') || lowerText.includes('') || lowerText.includes('')) {
      response = mockAIResponses['risk alert'];
    } else if (lowerText.includes('calendar') || lowerText.includes('')) {
      response = mockAIResponses['farming calendar'];
    } else if (lowerText.includes('scheme') || lowerText.includes('')) {
      response = mockAIResponses['local schemes'];
    } else {
      // Generic response
      response = '    !                  ?';
    }
    
    addMessage({
      id: Date.now().toString(),
      text: response,
      sender: 'ai',
      timestamp: new Date(),
      type: 'text',
      metadata: { language }
    });
  };

  const sendMessageViaAPI = async (text: string, language: string) => {
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/v1/chat/send`, {
        message: text,
        language,
        user_id: user?.id
      });
      
      if (response.data.success) {
        addMessage({
          id: Date.now().toString(),
          text: response.data.response,
          sender: 'ai',
          timestamp: new Date(),
          type: 'text',
          metadata: { language }
        });
      }
    } catch (error) {
      console.error('API call failed:', error);
      throw error;
    }
  };

  const clearMessages = () => {
    setMessages([]);
  };

  const value: ChatContextType = {
    messages,
    isLoading,
    sendMessage,
    clearMessages,
    isConnected
  };

  return (
    <ChatContext.Provider value={value}>
      {children}
    </ChatContext.Provider>
  );
};
